import {
  Flex,
  Text,
  Image,
  Spacer,
  Divider,
  Stack,
  useToast,
  Center,
  Box,
  Skeleton,
} from "@chakra-ui/react";
import { FC } from "react";
import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import MeProtocolButton from "../MeProtocolButton";
import { useNavigate } from "react-router-dom";
import Images_Icons from "../../constants/icons-images";
import { Dispatch } from "redux";
import { RootState } from "../../redux/store";
import { brands } from "../../constants/brandsdummyJSON";
import { nanoid } from "nanoid";
import { useUnfollowBrandMutation } from "../../redux/rtkQuery/api";
import { AuthReducerProps } from "../../interfaces";
import MeProtocolToast from "../../hooks/MeProtocolToast";
import { getBrandsUserFollowing } from "../../redux/UserSlice";
import { GLOBAL_PREFIX } from "../../constants";

const MyBrands: FC<any> = ({ item }): JSX.Element => {
  const { token }: AuthReducerProps = useSelector((state: RootState) => state.authReducer);
  const { root, bodyText, headText } = useStyles();
  const dispatch = useDispatch<Dispatch>();

  const [followLoading, setFollowLoading] = useState<boolean>(false);
  let navigate = useNavigate();
  const toast = useToast();
  const [unfollowBrand] = useUnfollowBrandMutation();

  // -------------------------------------------------UNFOLLOW BRAND FN-----------------------------------------------
  async function UnFollowBrand() {
    if (!token) {
      navigate(`${GLOBAL_PREFIX}/login`);
    }
    setFollowLoading(true);
    await unfollowBrand({ brandId: item?.brand?.id })
      .then((res: any) => {
        if (res?.data?.statusCode === 200) {
          MeProtocolToast(toast, "Brand Unfollowed", 5000);
          dispatch(getBrandsUserFollowing({ token }));
        }
      })
      .catch((e: any | unknown) => {
        console.log(e, "unFollowBrandERR");
      })
      .finally(() => {
        setFollowLoading(false);
      });
  }

  return (
    <Stack>
      <Flex {...root} flexDir="row" align={"center"}>
        {/* ------------------------IMAGE---------------------------------- */}

        <Flex w="40%" h={"70%"} flexDir="row" align={"center"} justify="space-between">
          <Flex w="250px" h="150px" flexDir="column" bg={"grey"}>
            {/* {[1, 2, 3, 4]?.map((e: any) => ( */}
            <Box key={nanoid()} role="group">
              <Skeleton isLoaded={true} mr={"20px"} overflow={"hidden"} w={"360px"} h={"180px"}>
                <Center
                  w="250px"
                  h="150px"
                  bg={"#0000003D"}
                  pos="absolute"
                  as={Image}
                  // background={`url(${item?.brand?.banners[0]}) center/cover no-repeat`}
                  overflow={"hidden"}
                  color={"rgba(153,153,153,0.66)"}
                  _hover={{ color: "black" }}
                  fallbackSrc={Images_Icons.appLogoPlaceholder}
                  fallbackStrategy={"beforeLoadOrError"}
                >
                  {item?.brand?.white_logo && (
                    <Image
                      w={"60px"}
                      h={"60px"}
                      position="relative"
                      borderRadius={"5px"}
                      src={item?.brand?.white_logo}
                      objectFit={"contain"}
                      fallbackSrc={Images_Icons.appLogoPlaceholder}
                      fallbackStrategy={"onError"}
                    />
                  )}
                </Center>
              </Skeleton>
            </Box>
            {/* ))} */}
          </Flex>
          <Spacer />
          <Flex flexDir="column" w="250px" h="150px" ml={"20px"}>
            {/* -------------------- Brand Name --------------------------------- */}
            <Text {...headText} noOfLines={1} textTransform="uppercase">
              {item?.brand?.brandName}
            </Text>
            {/* <Spacer /> */}
            {/* --------------------- Brand Description -------------------------- */}
            <Text {...bodyText} noOfLines={4}>
              {item?.brand?.description}
            </Text>
          </Flex>
        </Flex>

        {/* ------------------------UNFOLLOW---------------------------------- */}
        <Flex w="30%" h={"100%"} align="flex-end" flexDir={"column"}>
          <MeProtocolButton
            name={"Unfollow"}
            isLoading={followLoading}
            rest={{
              w: "146px",
              fontSize: "16px",
              bg: "dark",
              borderWidth: 1,
              borderColor: "light",
              color: "light",
              className: "unfollow",
              borderRadius: 0,
            }}
            onClick={UnFollowBrand}
          />
          <Text as="button" {...bodyText}>
            Contact Brand
          </Text>
          <Text as="button" {...bodyText}>
            Report Brand
          </Text>
        </Flex>
      </Flex>
      <Divider />
    </Stack>
  );
};

export default MyBrands;

const useStyles = () => {
  return {
    root: {
      w: "100%",
      h: "150px",
      justify: "space-between",
    },
    headText: {
      fontSize: "18px",
    },
    bodyText: {
      fontSize: "16px",
      color: "#999999",
      mt: "2px",
    },
  };
};
